import React from "react";

const Project = (props) => {
  return (
    <>
      {props.index % 2 == 1 ? (
        <div className="projectHolder">
          <a href={props.link} target="_blank" rel="noreferrer">
            <div className={`projectCardBase projectCard `}>
              <img
                src={process.env.PUBLIC_URL + props.image}
                alt={props.title}
                className="projectImage"
              />
            </div>
          </a>
          <div className={`projectDescription ${"projectDescription-v2"}`}>
            <h1>{props.title}</h1>
            {props.text}
          </div>{" "}
        </div>
      ) : (
        <div className="projectHolder">
          <div className={`projectDescription ${"projectDescription-v2"}`}>
            <h1>{props.title}</h1>
            {props.text}
          </div>{" "}
          <a href={props.link} target="_blank" rel="noreferrer">
            <div className={`projectCardBase projectCard `}>
              <img
                src={process.env.PUBLIC_URL + props.image}
                alt={props.title}
                className="projectImage"
              />
            </div>
          </a>
        </div>
      )}
    </>
  );
};
export default Project;
